<template>
  <div class="wrapper">
    <h2 class="main__title">Users</h2>
    <div class="main__container" v-if="users">
      <div class="table__container">
        <div class="table__row heading">
          <div class="row__item row-id" @click="sortId">
            Id
            <img class="row__img" src="@/assets/images/up-down.svg" alt="" />
          </div>
          <div class="row__item" @click="sortByName">
            name <img class="row__img" src="@/assets/images/up-down.svg" alt="" />
          </div>
          <div class="row__item">email</div>
          <div class="row__item">address</div>
          <div class="row__item">phone number</div>
        </div>
        <div class="table__row" v-for="(user, index) in users" :key="index">
          <div class="row__item row-id">{{ user.id }}</div>
          <div class="row__item">{{ user.name }}</div>
          <div class="row__item">{{ user.email }}</div>
          <div class="row__item">{{ user.address.street }}, {{ user.address.suite }}</div>
          <div class="row__item">{{ user.phone }}</div>
        </div>
      </div>
    </div>
  </div>
</template>

<script setup>
import { useUsers } from "@/stores/users.js";
import { computed, ref } from "vue";
const userStore = useUsers();
userStore.getUsers();
let users = computed(() => userStore.users);
console.log(users);
// const users = ref(userStore.users);

function sortId() {
  if (users.value[0].id != 1) {
    users.value = users.value.sort((a, b) => a.id - b.id);
  } else {
    users.value = users.value.reverse();
  }
}
function sortByName() {
  users.value = users.value.sort((el, el2) => el.name.localeCompare(el2.name));
}
</script>

<style lang="scss" scoped></style>
