import { ref, computed } from 'vue'
import { defineStore } from 'pinia'
import axios from 'axios'
export const useUsers = defineStore({
  id:'users',
  state:()=>({
    url:import.meta.env.VITE_URL,
    users:null
  }),
  actions:{
   async getUsers(){
      const res = await axios.get(`${this.url}users`)
      this.users = res.data
    }
  },
  getters:{
    
  },
  
})
